@mixin dark-background-colors {
  color: $body-color-dark-theme;
  @include text-emphasis-variant(".text-low-contrast", $text-low-contrast-dark-theme);
  @include text-emphasis-variant(".text-high-contrast", $text-high-contrast-dark-theme);

  @if (mixin_exists(button-borderless-variant)) {
    .btn-borderless.btn-default {
      @include button-borderless-variant($btn-borderless-color-dark-theme);
    }
  }
}

@mixin light-background-colors {
  color: $body-color-light-theme;
  @include text-emphasis-variant(".text-low-contrast", $text-low-contrast-light-theme);
  @include text-emphasis-variant(".text-high-contrast", $text-high-contrast-light-theme);

  @if (mixin_exists(button-borderless-variant)) {
    .btn-borderless.btn-default {
      @include button-borderless-variant($btn-borderless-color-light-theme);
    }
  }
}
